body {
	font-family: $Futuras;
	color: rgba($black, 0.5);
	@include f(20px,500,1.5);
	min-width: 1260px;
	@include font-smoothing;
	letter-spacing: 0;
	background-color: $white;
	&.is-dark {
		color: rgba($white, .5);
		background-color: $darkBg;
		.title {
			color: $white; } }
	&.no-min-width {
		min-width: inherit; } }

.title {
	font-family: $Futuras;
	@include f(30px,500,1.333);
	color: $black;
	@include css-lock(21,30,1024,1440); }

.light {
	font-weight: 300; }

.prev-head {
	margin-top: 60px; }

.text-upper {
	text-transform: uppercase; }
